// /src/components/layout/Footer.js
import React from 'react';
import { Link } from 'react-router-dom';
import { Github, Mail, ExternalLink } from 'lucide-react';
import PropTypes from 'prop-types';
import { PROJECTS } from '../../utils/constants';

const Footer = ({ projectId = null }) => {
  // Get current date for copyright
  const currentYear = new Date().getFullYear();
  
  // Get project data if a projectId is provided
  const projectData = projectId ? PROJECTS.find(p => p.id === projectId) : null;
  
  // Set the text based on whether this is a project-specific page
  const projectText = projectData 
    ? `${projectData.acronym} Citation Dashboard | ${projectData.name}`
    : 'Scientific Models Citation Dashboard';
  
  // Determine data sources based on project
  const dataSources = projectData
    ? 'Google Scholar, Web of Science, Scopus, GitHub'
    : 'Multiple citation databases and repositories';
    
  // Get last updated date
  const lastUpdated = 'May 04, 2025';
  
  return (
    <footer className="bg-white border-t border-gray-200 mt-8">
      <div className="max-w-7xl mx-auto px-4 py-6">
        {/* Main footer content */}
        <div className="flex flex-col md:flex-row justify-between items-center gap-6">
          <div className="flex flex-col items-center md:items-start">
            <div className="text-sm text-gray-500"> {currentYear} {projectText}</div>
            <div className="text-xs text-gray-400 mt-1">Data sources: {dataSources} | Last updated: {lastUpdated}</div>
          </div>
          
          <div className="flex gap-4">
            {projectData && projectData.github && (
              <a
                href={`https://github.com/${projectData.github}`}
                target="_blank"
                rel="noopener noreferrer"
                className="text-gray-500 hover:text-gray-700"
                title={`${projectData.acronym} GitHub Repository`}
              >
                <Github size={20} />
              </a>
            )}
            
            <a
              href="mailto:support@scientificmodels.org"
              className="text-gray-500 hover:text-gray-700"
              title="Contact Support"
            >
              <Mail size={20} />
            </a>
            
            <a
              href="https://scientificmodels.org"
              target="_blank"
              rel="noopener noreferrer"
              className="text-gray-500 hover:text-gray-700"
              title="Main Website"
            >
              <ExternalLink size={20} />
            </a>
          </div>
        </div>
        
        {/* Navigation links */}
        <div className="border-t border-gray-200 mt-6 pt-4">
          <div className="flex flex-wrap justify-center gap-x-6 gap-y-2 text-xs text-gray-500">
            <Link to="/" className="hover:text-gray-700">Main Dashboard</Link>
            <Link to="/comparison" className="hover:text-gray-700">Model Comparison</Link>
            <Link to="/geographic-impact" className="hover:text-gray-700">Geographic Impact</Link>
            <Link to="/domains" className="hover:text-gray-700">Research Domains</Link>
            
            {/* Project links */}
            {PROJECTS.map((project) => (
              <Link 
                key={project.id} 
                to={project.routePath}
                className="hover:text-gray-700"
              >
                {project.acronym}
              </Link>
            ))}
            
            {/* Static pages */}
            <Link to="/about" className="hover:text-gray-700">About</Link>
            <Link to="/methodology" className="hover:text-gray-700">Methodology</Link>
            <Link to="/privacy" className="hover:text-gray-700">Privacy Policy</Link>
            <Link to="/terms" className="hover:text-gray-700">Terms of Use</Link>
          </div>
        </div>
      </div>
    </footer>
  );
};

Footer.propTypes = {
  projectId: PropTypes.string
};

export default Footer;