// /src/data/shared/dataUtils.js
/**
 * Utility functions for processing data for visualizations
 */

/**
 * Parse CSV data into objects
 * @param {string} csvData - Raw CSV data as string
 * @returns {Array} - Array of objects with headers as keys
 */
export const parseCSVData = (csvData) => {
    // Handle empty data
    if (!csvData) return [];
  
    // Split by lines and filter out empty lines
    const lines = csvData.split('\n').filter(line => line.trim());
    
    // No data or only header
    if (lines.length <= 1) return [];
    
    // Extract headers from first line
    const headers = lines[0].split(',').map(header => header.trim());
    
    // Parse data rows
    return lines.slice(1).map(line => {
      const values = line.split(',');
      const row = {};
      
      // Map values to headers
      headers.forEach((header, index) => {
        let value = values[index] ? values[index].trim() : '';
        
        // Try to convert numeric values
        if (!isNaN(value) && value !== '') {
          value = parseFloat(value);
        }
        
        row[header] = value;
      });
      
      return row;
    });
  };
  
  /**
   * Process raw citation data into format needed for dashboard
   * @param {Array} rawCitations - Raw citation data
   * @returns {Object} - Processed data for dashboard components
   */
  export const processRawCitationData = (rawCitations) => {
    // Handle empty data
    if (!rawCitations || !Array.isArray(rawCitations) || rawCitations.length === 0) {
      return {
        citationData: [],
        domainData: [],
        engagementData: [],
        citationTypeData: [],
        journalData: []
      };
    }
    
    // Extract years from data for time series
    const years = extractYearsFromCitations(rawCitations);
    
    // Process annual citation data
    const citationData = processAnnualCitationData(rawCitations, years);
    
    // Extract domains from citations
    const domainData = extractDomains(rawCitations);
    
    // Process engagement levels
    const engagementData = processEngagementLevels(rawCitations);
    
    // Process citation types
    const citationTypeData = processCitationTypes(rawCitations);
    
    // Process journal distribution
    const journalData = processJournalDistribution(rawCitations);
    
    return {
      citationData,
      domainData,
      engagementData,
      citationTypeData,
      journalData
    };
  };
  
  /**
   * Extract unique years from citation data
   * @param {Array} citations - Citation data
   * @returns {Array} - Sorted array of years
   */
  const extractYearsFromCitations = (citations) => {
    const years = new Set();
    
    citations.forEach(citation => {
      if (citation.year) {
        years.add(parseInt(citation.year));
      }
    });
    
    return Array.from(years).sort();
  };
  
  /**
   * Process annual citation data for time series
   * @param {Array} citations - Citation data
   * @param {Array} years - Array of years to process
   * @returns {Array} - Processed annual citation data
   */
  const processAnnualCitationData = (citations, years) => {
    // Initialize data structure
    const result = years.map(year => ({
      year,
      annual: 0,
      cumulative: 0,
      peerReviewed: 0,
      other: 0,
      growthRate: null
    }));
    
    // Count citations by year
    citations.forEach(citation => {
      const year = parseInt(citation.year);
      const yearIndex = result.findIndex(item => item.year === year);
      
      if (yearIndex >= 0) {
        result[yearIndex].annual += 1;
        
        // Check if peer-reviewed
        const isPeerReviewed = isPeerReviewedCitation(citation);
        
        if (isPeerReviewed) {
          result[yearIndex].peerReviewed += 1;
        } else {
          result[yearIndex].other += 1;
        }
      }
    });
    
    // Calculate cumulative counts and growth rates
    let cumulative = 0;
    
    result.forEach((item, index) => {
      cumulative += item.annual;
      item.cumulative = cumulative;
      
      // Calculate growth rate if not the first year
      if (index > 0 && result[index - 1].annual > 0) {
        const prevYear = result[index - 1].annual;
        const currentYear = item.annual;
        item.growthRate = ((currentYear - prevYear) / prevYear) * 100;
      }
    });
    
    return result;
  };
  
  /**
   * Determine if a citation is from a peer-reviewed source
   * @param {Object} citation - Citation object
   * @returns {boolean} - True if peer-reviewed
   */
  const isPeerReviewedCitation = (citation) => {
    // Check type field if available
    if (citation.type) {
      const type = citation.type.toLowerCase();
      return type.includes('journal') || type.includes('peer') || type === 'journal article';
    }
    
    // Check journal field
    if (citation.journal) {
      const journal = citation.journal.toLowerCase();
      return !journal.includes('conference') && !journal.includes('workshop') && !journal.includes('symposium');
    }
    
    // Check source field if available
    if (citation.source) {
      const source = citation.source.toLowerCase();
      return source.includes('journal') && !source.includes('conference');
    }
    
    // Default to false if unsure
    return false;
  };
  
  /**
   * Extract domains from citations based on keywords
   * @param {Array} citations - Citation data
   * @returns {Array} - Domain data for visualization
   */
  const extractDomains = (citations) => {
    // Domain keyword mappings
    const domainKeywords = {
      'Hydrology': ['river', 'watershed', 'hydrology', 'flow', 'discharge', 'water', 'stream'],
      'Climate Science': ['climate', 'warming', 'temperature', 'precipitation', 'weather'],
      'Computer Science': ['algorithm', 'computation', 'parallel', 'performance', 'efficiency'],
      'Water Resources': ['water', 'management', 'resource', 'planning', 'supply'],
      'Flood Prediction': ['flood', 'inundation', 'forecasting', 'prediction', 'warning'],
      'Carbon Science': ['carbon', 'flux', 'emission', 'sink', 'dioxide', 'methane'],
      'Atmospheric Chemistry': ['atmosphere', 'chemical', 'aerosol', 'pollution', 'air quality'],
      'Physical Oceanography': ['ocean', 'circulation', 'current', 'gyre', 'sea'],
      'Sea Level Studies': ['sea level', 'rise', 'coastal', 'inundation'],
      'Ice Sheet Dynamics': ['ice sheet', 'glacier', 'polar', 'cryosphere', 'greenland', 'antarctica']
    };
    
    // Count domains based on keywords in title and abstract
    const domainCounts = {};
    
    citations.forEach(citation => {
      // Combine title and abstract for keyword search
      const text = `${citation.title || ''} ${citation.abstract || ''}`.toLowerCase();
      
      // Check each domain
      Object.entries(domainKeywords).forEach(([domain, keywords]) => {
        // Check if any keyword is present
        const hasDomainKeywords = keywords.some(keyword => text.includes(keyword));
        
        if (hasDomainKeywords) {
          domainCounts[domain] = (domainCounts[domain] || 0) + 1;
        }
      });
    });
    
    // Convert to array and sort by count
    const domainData = Object.entries(domainCounts)
      .map(([name, value]) => {
        const percentage = (value / citations.length) * 100;
        return { name, value, percentage };
      })
      .sort((a, b) => b.value - a.value);
    
    return domainData;
  };
  
  /**
   * Process engagement levels from citation data
   * @param {Array} citations - Citation data
   * @returns {Array} - Engagement level data for visualization
   */
  const processEngagementLevels = (citations) => {
    // Define engagement levels
    const levels = [
      { name: 'Level 1: Simple Citation', value: 0 },
      { name: 'Level 2: Data Usage', value: 0 },
      { name: 'Level 3: Model Adaptation', value: 0 },
      { name: 'Level 4: Foundation', value: 0 }
    ];
    
    // Count papers by engagement level
    citations.forEach(citation => {
      // Determine engagement level based on available data
      const level = determineEngagementLevel(citation);
      levels[level - 1].value += 1;
    });
    
    return levels;
  };
  
  /**
   * Determine engagement level for a citation
   * @param {Object} citation - Citation object
   * @returns {number} - Engagement level (1-4)
   */
  const determineEngagementLevel = (citation) => {
    // Check if engagement level is already specified
    if (citation.engagementLevel) {
      return parseInt(citation.engagementLevel);
    }
    
    // Check type field for clues
    if (citation.type) {
      const type = citation.type.toLowerCase();
      
      if (type.includes('foundation') || type.includes('level 4')) {
        return 4;
      }
      
      if (type.includes('adaptation') || type.includes('extension') || type.includes('level 3')) {
        return 3;
      }
      
      if (type.includes('data usage') || type.includes('level 2')) {
        return 2;
      }
    }
    
    // Check abstract for clues
    if (citation.abstract) {
      const abstract = citation.abstract.toLowerCase();
      
      // Foundation keywords
      if (abstract.includes('based on') || abstract.includes('foundation') || 
          abstract.includes('framework') || abstract.includes('built upon')) {
        return 4;
      }
      
      // Adaptation keywords
      if (abstract.includes('modified') || abstract.includes('extended') || 
          abstract.includes('adapted') || abstract.includes('improved') ||
          abstract.includes('enhanced')) {
        return 3;
      }
      
      // Data usage keywords
      if (abstract.includes('using') || abstract.includes('applied') || 
          abstract.includes('data from') || abstract.includes('method from')) {
        return 2;
      }
    }
    
    // Default to level 1
    return 1;
  };
  
  /**
   * Process citation types from citation data
   * @param {Array} citations - Citation data
   * @returns {Array} - Citation type data for visualization
   */
  const processCitationTypes = (citations) => {
    // Define citation types
    const types = {
      'Peer-Reviewed Journals': 0,
      'Conference Papers': 0,
      'Academic Theses': 0,
      'Technical Reports': 0,
      'Online Resources': 0,
      'Popular Press': 0
    };
    
    // Count citations by type
    citations.forEach(citation => {
      const type = determineCitationType(citation);
      types[type] += 1;
    });
    
    // Convert to array with percentages
    const citationTypeData = Object.entries(types)
      .map(([name, value]) => {
        const percentage = (value / citations.length) * 100;
        return { name, value, percentage };
      })
      .sort((a, b) => b.value - a.value);
    
    return citationTypeData;
  };
  
  /**
   * Determine citation type based on available data
   * @param {Object} citation - Citation object
   * @returns {string} - Citation type
   */
  const determineCitationType = (citation) => {
    // Check type field if available
    if (citation.type) {
      const type = citation.type.toLowerCase();
      
      if (type.includes('journal') || type.includes('article')) {
        return 'Peer-Reviewed Journals';
      }
      
      if (type.includes('conference') || type.includes('proceedings')) {
        return 'Conference Papers';
      }
      
      if (type.includes('thesis') || type.includes('dissertation')) {
        return 'Academic Theses';
      }
      
      if (type.includes('report') || type.includes('technical')) {
        return 'Technical Reports';
      }
      
      if (type.includes('press') || type.includes('magazine') || type.includes('news')) {
        return 'Popular Press';
      }
      
      if (type.includes('web') || type.includes('online')) {
        return 'Online Resources';
      }
    }
    
    // Check journal field
    if (citation.journal) {
      const journal = citation.journal.toLowerCase();
      
      if (journal.includes('conference') || journal.includes('proceedings')) {
        return 'Conference Papers';
      }
      
      if (journal.includes('thesis') || journal.includes('dissertation')) {
        return 'Academic Theses';
      }
      
      if (journal.includes('report')) {
        return 'Technical Reports';
      }
      
      // Default to journal if no other matches
      return 'Peer-Reviewed Journals';
    }
    
    // Default to online resources if unsure
    return 'Online Resources';
  };
  
  /**
   * Process journal distribution from citation data
   * @param {Array} citations - Citation data
   * @returns {Array} - Journal data for visualization
   */
  const processJournalDistribution = (citations) => {
    // Count citations by journal
    const journalCounts = {};
    
    citations.forEach(citation => {
      if (citation.journal) {
        const journal = citation.journal.trim();
        journalCounts[journal] = (journalCounts[journal] || 0) + 1;
      }
    });
    
    // Convert to array and sort by count
    const journalData = Object.entries(journalCounts)
      .map(([name, count]) => ({ name, count }))
      .sort((a, b) => b.count - a.count)
      .slice(0, 10); // Keep only top 10 journals
    
    return journalData;
  };
  
  /**
   * Calculate citation growth metrics
   * @param {Array} citationData - Time series citation data
   * @returns {Object} - Growth metrics
   */
  export const calculateGrowthMetrics = (citationData) => {
    // Handle empty data
    if (!citationData || citationData.length === 0) {
      return {
        totalCitations: 0,
        annualGrowthRate: 0,
        averageAnnualCitations: 0,
        peakYear: null,
        peakCitations: 0
      };
    }
    
    // Find the most recent complete year
    const currentYear = new Date().getFullYear();
    const completeYears = citationData.filter(item => item.year < currentYear);
    
    // Calculate total citations
    const totalCitations = citationData.reduce((sum, item) => sum + item.annual, 0);
    
    // Calculate average annual citations
    const yearCount = completeYears.length;
    const averageAnnualCitations = yearCount > 0 ? 
      completeYears.reduce((sum, item) => sum + item.annual, 0) / yearCount : 0;
    
    // Find peak year
    const peakYear = citationData.reduce((max, item) => 
      item.annual > (max.annual || 0) ? item : max, {});
    
    // Calculate growth rate over the past year
    let annualGrowthRate = 0;
    if (completeYears.length >= 2) {
      const lastYear = completeYears[completeYears.length - 1];
      const previousYear = completeYears[completeYears.length - 2];
      
      if (previousYear.annual > 0) {
        annualGrowthRate = ((lastYear.annual - previousYear.annual) / previousYear.annual) * 100;
      }
    }
    
    return {
      totalCitations,
      annualGrowthRate,
      averageAnnualCitations,
      peakYear: peakYear.year,
      peakCitations: peakYear.annual
    };
  };
  
  /**
   * Format a number with unit suffix (K, M, B)
   * @param {number} value - Value to format
   * @returns {string} Formatted value with suffix
   */
  export const formatNumberWithSuffix = (value) => {
    if (value >= 1000000000) {
      return (value / 1000000000).toFixed(1) + 'B';
    }
    if (value >= 1000000) {
      return (value / 1000000).toFixed(1) + 'M';
    }
    if (value >= 1000) {
      return (value / 1000).toFixed(1) + 'K';
    }
    return value.toString();
  };