// /src/data/services/dataService.js
import { parseCSVData, processRawCitationData } from '../shared/dataUtils';
import api from '../../utils/api';

// Mock data for development
import rapidMockData from '../rapid/rapidData';
import cmsFluxMockData from '../cms-flux/cmsFluxData';
import eccoMockData from '../ecco/eccoData';
import issmMockData from '../issm/issmData';
import momoChemMockData from '../momo-chem/momoChemData';

// Flag to control whether to use mock data or API
const USE_MOCK_DATA = process.env.REACT_APP_USE_MOCK_DATA === 'true';

/**
 * Fetch data for all projects
 * @returns {Promise<Array>} - Promise resolving to array of project data
 */
export const fetchAllProjectsData = async () => {
  if (USE_MOCK_DATA) {
    // Combine all mock data and extract summary info
    return [
      extractProjectSummary(rapidMockData),
      extractProjectSummary(cmsFluxMockData),
      extractProjectSummary(eccoMockData),
      extractProjectSummary(issmMockData),
      extractProjectSummary(momoChemMockData)
    ];
  } else {
    try {
      const response = await api.get('/projects/summary');
      return response.data;
    } catch (error) {
      console.error('Error fetching all projects data:', error);
      throw error;
    }
  }
};

/**
 * Extract project summary data from complete data
 * @param {Object} projectData - Complete project data
 * @returns {Object} - Summary data for project card
 */
const extractProjectSummary = (projectData) => {
  return {
    id: projectData.id,
    name: projectData.name,
    acronym: projectData.acronym,
    totalCitations: projectData.metrics.totalCitations,
    citationGrowth: projectData.metrics.citationGrowthRate,
    impactScore: projectData.metrics.impactScore,
    researchDomains: projectData.topDomains,
    color: projectData.color,
    routePath: projectData.routePath,
    lastUpdated: projectData.lastUpdated,
    annualCitations: projectData.citationData.reduce((acc, item) => {
      acc[item.year] = item.annual;
      return acc;
    }, {})
  };
};

/**
 * Fetch citation data from CSV file for a project
 * @param {string} projectId - Project identifier
 * @param {string} filename - CSV file name
 * @returns {Promise<Array>} - Promise resolving to citation data
 */
const fetchCitationCSV = async (projectId, filename) => {
  if (USE_MOCK_DATA) {
    // Simulated delay for mock data
    await new Promise(resolve => setTimeout(resolve, 500));
    
    // Return appropriate mock data based on project ID
    switch (projectId) {
      case 'rapid':
        return rapidMockData.rawCitations;
      case 'cms-flux':
        return cmsFluxMockData.rawCitations;
      case 'ecco':
        return eccoMockData.rawCitations;
      case 'issm':
        return issmMockData.rawCitations;
      case 'momo-chem':
        return momoChemMockData.rawCitations;
      default:
        throw new Error(`Unknown project ID: ${projectId}`);
    }
  } else {
    try {
      const response = await fetch(`/data/${projectId}/${filename}`);
      const csvData = await response.text();
      return parseCSVData(csvData);
    } catch (error) {
      console.error(`Error fetching ${projectId} citation data:`, error);
      throw error;
    }
  }
};

/**
 * Process citation data for visualization
 * @param {Array} rawCitations - Raw citation data
 * @returns {Object} - Processed data for dashboard components
 */
const processCitationData = (rawCitations) => {
  // Use shared utility to process data
  return processRawCitationData(rawCitations);
};

/**
 * Fetch RAPID data
 * @returns {Promise<Object>} - Promise resolving to RAPID data
 */
export const fetchRapidData = async () => {
  if (USE_MOCK_DATA) {
    // Simulated delay for mock data
    await new Promise(resolve => setTimeout(resolve, 500));
    return rapidMockData;
  } else {
    try {
      // Fetch raw citation data from CSV
      const rawCitations = await fetchCitationCSV('rapid', 'rapid_citations.csv');
      
      // Fetch additional data from API
      const response = await api.get('/projects/rapid');
      
      // Process and combine data
      const processedData = processCitationData(rawCitations);
      
      return {
        ...response.data,
        ...processedData
      };
    } catch (error) {
      console.error('Error fetching RAPID data:', error);
      throw error;
    }
  }
};

