// /src/contexts/DataContext.js
import React, { createContext, useState, useContext, useCallback } from 'react';
import { 
  fetchAllProjectsData, 
  fetchRapidData, 
  fetchCmsFluxData, 
  fetchEccoData, 
  fetchIssmData, 
  fetchMomoChemData 
} from '../data/services/dataService';

// Create data context
const DataContext = createContext();

// Data provider component
export const DataProvider = ({ children }) => {
  // States for each data type
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [allProjectsData, setAllProjectsData] = useState(null);
  const [rapidData, setRapidData] = useState(null);
  const [cmsFluxData, setCmsFluxData] = useState(null);
  const [eccoData, setEccoData] = useState(null);
  const [issmData, setIssmData] = useState(null);
  const [momoChemData, setMomoChemData] = useState(null);
  
  // Filters state
  const [filters, setFilters] = useState({
    yearRange: [2010, new Date().getFullYear()],
    citationType: 'all',
    researchDomain: 'all',
    engagementLevel: 'all'
  });
  
  // Load all projects data
  const loadAllProjectsData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await fetchAllProjectsData();
      setAllProjectsData(data);
      setError(null);
      return data;
    } catch (err) {
      console.error('Error fetching all projects data:', err);
      setError('Failed to load projects data. Please try again later.');
      return null;
    } finally {
      setIsLoading(false);
    }
  }, []);
  
  // Load RAPID data
  const loadRapidData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await fetchRapidData();
      setRapidData(data);
      setError(null);
      return data;
    } catch (err) {
      console.error('Error fetching RAPID data:', err);
      setError('Failed to load RAPID data. Please try again later.');
      return null;
    } finally {
      setIsLoading(false);
    }
  }, []);
  
  // Load CMS-Flux data
  const loadCmsFluxData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await fetchCmsFluxData();
      setCmsFluxData(data);
      setError(null);
      return data;
    } catch (err) {
      console.error('Error fetching CMS-Flux data:', err);
      setError('Failed to load CMS-Flux data. Please try again later.');
      return null;
    } finally {
      setIsLoading(false);
    }
  }, []);
  
  // Load ECCO data
  const loadEccoData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await fetchEccoData();
      setEccoData(data);
      setError(null);
      return data;
    } catch (err) {
      console.error('Error fetching ECCO data:', err);
      setError('Failed to load ECCO data. Please try again later.');
      return null;
    } finally {
      setIsLoading(false);
    }
  }, []);
  
  // Load ISSM data
  const loadIssmData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await fetchIssmData();
      setIssmData(data);
      setError(null);
      return data;
    } catch (err) {
      console.error('Error fetching ISSM data:', err);
      setError('Failed to load ISSM data. Please try again later.');
      return null;
    } finally {
      setIsLoading(false);
    }
  }, []);
  
  // Load MOMO-CHEM data
  const loadMomoChemData = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await fetchMomoChemData();
      setMomoChemData(data);
      setError(null);
      return data;
    } catch (err) {
      console.error('Error fetching MOMO-CHEM data:', err);
      setError('Failed to load MOMO-CHEM data. Please try again later.');
      return null;
    } finally {
      setIsLoading(false);
    }
  }, []);
  
  // Apply filters to data
  const applyFilters = useCallback((newFilters) => {
    setFilters(prevFilters => ({
      ...prevFilters,
      ...newFilters
    }));
  }, []);
  
  // Reset filters to default
  const resetFilters = useCallback(() => {
    setFilters({
      yearRange: [2010, new Date().getFullYear()],
      citationType: 'all',
      researchDomain: 'all',
      engagementLevel: 'all'
    });
  }, []);
  
  // Clear all loaded data
  const clearAllData = useCallback(() => {
    setAllProjectsData(null);
    setRapidData(null);
    setCmsFluxData(null);
    setEccoData(null);
    setIssmData(null);
    setMomoChemData(null);
  }, []);
  
  // Provide data and data control functions
  return (
    <DataContext.Provider
      value={{
        // Data states
        isLoading,
        error,
        allProjectsData,
        rapidData,
        cmsFluxData,
        eccoData,
        issmData,
        momoChemData,
        filters,
        
        // Data loading functions
        loadAllProjectsData,
        loadRapidData,
        loadCmsFluxData,
        loadEccoData,
        loadIssmData,
        loadMomoChemData,
        
        // Filter functions
        applyFilters,
        resetFilters,
        clearAllData
      }}
    >
      {children}
    </DataContext.Provider>
  );
};

// Custom hook to use data
export const useData = () => {
  const context = useContext(DataContext);
  if (!context) {
    throw new Error('useData must be used within a DataProvider');
  }
  return context;
};

export default DataContext;