// /src/utils/api.js
import { API_ENDPOINTS } from './constants';

/**
 * API utility for making requests to the backend
 */
const api = {
  /**
   * Build the full URL for an endpoint
   * @param {string} endpoint - API endpoint path
   * @returns {string} - Full URL
   */
  buildUrl: (endpoint) => {
    return `${API_ENDPOINTS.base}${endpoint}`;
  },
  
  /**
   * Make a GET request
   * @param {string} endpoint - API endpoint path
   * @param {Object} params - Query parameters
   * @returns {Promise} - Promise that resolves to response data
   */
  get: async (endpoint, params = {}) => {
    try {
      // Build URL with query parameters
      const url = new URL(api.buildUrl(endpoint));
      
      // Add query parameters
      Object.keys(params).forEach(key => {
        url.searchParams.append(key, params[key]);
      });
      
      // Make the request
      const response = await fetch(url.toString(), {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          ...api.getAuthHeaders()
        }
      });
      
      // Check for errors
      if (!response.ok) {
        throw new Error(`API error: ${response.status} ${response.statusText}`);
      }
      
      // Parse response
      return await response.json();
    } catch (error) {
      console.error('API GET request failed:', error);
      throw error;
    }
  },
  
  /**
   * Make a POST request
   * @param {string} endpoint - API endpoint path
   * @param {Object} data - Request body data
   * @returns {Promise} - Promise that resolves to response data
   */
  post: async (endpoint, data = {}) => {
    try {
      // Make the request
      const response = await fetch(api.buildUrl(endpoint), {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          ...api.getAuthHeaders()
        },
        body: JSON.stringify(data)
      });
      
      // Check for errors
      if (!response.ok) {
        throw new Error(`API error: ${response.status} ${response.statusText}`);
      }
      
      // Parse response
      return await response.json();
    } catch (error) {
      console.error('API POST request failed:', error);
      throw error;
    }
  },
  
  /**
   * Make a PUT request
   * @param {string} endpoint - API endpoint path
   * @param {Object} data - Request body data
   * @returns {Promise} - Promise that resolves to response data
   */
  put: async (endpoint, data = {}) => {
    try {
      // Make the request
      const response = await fetch(api.buildUrl(endpoint), {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          ...api.getAuthHeaders()
        },
        body: JSON.stringify(data)
      });
      
      // Check for errors
      if (!response.ok) {
        throw new Error(`API error: ${response.status} ${response.statusText}`);
      }
      
      // Parse response
      return await response.json();
    } catch (error) {
      console.error('API PUT request failed:', error);
      throw error;
    }
  },
  
  /**
   * Make a DELETE request
   * @param {string} endpoint - API endpoint path
   * @returns {Promise} - Promise that resolves to response data
   */
  delete: async (endpoint) => {
    try {
      // Make the request
      const response = await fetch(api.buildUrl(endpoint), {
        method: 'DELETE',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          ...api.getAuthHeaders()
        }
      });
      
      // Check for errors
      if (!response.ok) {
        throw new Error(`API error: ${response.status} ${response.statusText}`);
      }
      
      // Parse response (might be empty for DELETE)
      return response.status === 204 ? {} : await response.json();
    } catch (error) {
      console.error('API DELETE request failed:', error);
      throw error;
    }
  },
  
  /**
   * Get authentication headers
   * @returns {Object} - Authentication headers
   */
  getAuthHeaders: () => {
    // Get auth token from localStorage
    const token = localStorage.getItem('authToken');
    
    // Return auth headers if token exists
    return token ? { 'Authorization': `Bearer ${token}` } : {};
  }
};

export default api;