// /src/components/layout/Header.js
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { Search, Bell, Settings, Menu, X } from 'lucide-react';
import PropTypes from 'prop-types';
import { PROJECTS } from '../../utils/constants';

const Header = ({ title, subtitle, projectId = null, showSearch = true }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const navigate = useNavigate();

  // Get project data if a projectId is provided
  const projectData = projectId ? PROJECTS.find(p => p.id === projectId) : null;
  
  // Handle search submit
  const handleSearchSubmit = (e) => {
    e.preventDefault();
    
    if (searchQuery.trim()) {
      // Navigate to search page with query parameter
      navigate(`/search?q=${encodeURIComponent(searchQuery.trim())}`);
    }
  };
  
  // Toggle mobile menu
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  return (
    <header className="bg-white border-b border-gray-200 shadow-sm sticky top-0 z-10">
      <div className="max-w-7xl mx-auto px-4 py-4">
        <div className="flex justify-between items-center">
          {/* Logo and Title */}
          <div className="flex items-center gap-3">
            <Link to="/" className="flex items-center">
              {projectData ? (
                <div className="h-10 w-10 rounded-md flex items-center justify-center text-white" 
                     style={{ backgroundColor: projectData.color }}>
                  <span className="font-bold">{projectData.acronym.charAt(0)}</span>
                </div>
              ) : (
                <div className="h-10 w-10 bg-gray-700 rounded-md flex items-center justify-center text-white">
                  <span className="font-bold">SM</span>
                </div>
              )}
              
              <div className="ml-3">
                <h1 className="text-lg font-semibold text-gray-900">{title || 'Scientific Model Dashboard'}</h1>
                <div className="text-xs text-gray-500">{subtitle || 'Citation and Impact Analysis'}</div>
              </div>
            </Link>
          </div>
          
          {/* Desktop Navigation */}
          <div className="hidden md:flex gap-8">
            <Link to="/" className="text-gray-600 hover:text-gray-800 font-medium text-sm">Main Dashboard</Link>
            <Link to="/comparison" className="text-gray-600 hover:text-gray-800 font-medium text-sm">Comparison</Link>
            <Link to="/citations" className="text-gray-600 hover:text-gray-800 font-medium text-sm">Citations</Link>
            <Link to="/geographic-impact" className="text-gray-600 hover:text-gray-800 font-medium text-sm">Geographic</Link>
            <Link to="/domains" className="text-gray-600 hover:text-gray-800 font-medium text-sm">Domains</Link>
          </div>
          
          {/* Search, Notifications, Settings */}
          <div className="flex items-center gap-4">
            {showSearch && (
              <form onSubmit={handleSearchSubmit} className="relative w-64 hidden md:block">
                <div className="absolute inset-y-0 left-3 flex items-center pointer-events-none">
                  <Search size={16} className="text-gray-500" />
                </div>
                <input 
                  type="text" 
                  placeholder="Search..." 
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="w-full py-2 pl-10 pr-4 border border-gray-300 rounded-full text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" 
                />
              </form>
            )}
            
            <button className="text-gray-600 hover:text-gray-800 hidden md:block">
              <Bell size={20} />
            </button>
            <button className="text-gray-600 hover:text-gray-800 hidden md:block">
              <Settings size={20} />
            </button>
            
            {/* User avatar placeholder */}
            <div className="h-8 w-8 bg-gray-300 rounded-full flex items-center justify-center text-gray-700 font-semibold">
              U
            </div>
            
            {/* Mobile menu button */}
            <button className="text-gray-600 hover:text-gray-800 md:hidden ml-2" onClick={toggleMenu}>
              {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>
        </div>
        
        {/* Mobile menu */}
        {isMenuOpen && (
          <div className="md:hidden pt-4 pb-2 border-t border-gray-200 mt-4">
            <div className="flex flex-col space-y-4">
              <Link to="/" className="text-gray-600 hover:text-gray-800 font-medium text-sm py-2" onClick={toggleMenu}>Main Dashboard</Link>
              <Link to="/comparison" className="text-gray-600 hover:text-gray-800 font-medium text-sm py-2" onClick={toggleMenu}>Comparison</Link>
              <Link to="/citations" className="text-gray-600 hover:text-gray-800 font-medium text-sm py-2" onClick={toggleMenu}>Citations</Link>
              <Link to="/geographic-impact" className="text-gray-600 hover:text-gray-800 font-medium text-sm py-2" onClick={toggleMenu}>Geographic</Link>
              <Link to="/domains" className="text-gray-600 hover:text-gray-800 font-medium text-sm py-2" onClick={toggleMenu}>Domains</Link>
              
              {/* Mobile search */}
              {showSearch && (
                <form onSubmit={handleSearchSubmit} className="w-full pt-2">
                  <div className="relative">
                    <div className="absolute inset-y-0 left-3 flex items-center pointer-events-none">
                      <Search size={16} className="text-gray-500" />
                    </div>
                    <input 
                      type="text" 
                      placeholder="Search..." 
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className="w-full py-2 pl-10 pr-4 border border-gray-300 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent" 
                    />
                  </div>
                </form>
              )}
              
              {/* Quick links for project dashboards in mobile */}
              <div className="pt-2 border-t border-gray-200 mt-2">
                <div className="text-xs text-gray-500 py-2">Project Dashboards:</div>
                <div className="grid grid-cols-2 gap-2">
                  {PROJECTS.map((project) => (
                    <Link 
                      key={project.id}
                      to={project.routePath}
                      className="text-sm py-2 px-3 rounded-md border border-gray-200 flex items-center"
                      style={{ color: project.color }}
                      onClick={toggleMenu}
                    >
                      <div className="w-2 h-2 rounded-full mr-2" style={{ backgroundColor: project.color }}></div>
                      {project.acronym}
                    </Link>
                  ))}
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </header>
  );
};

Header.propTypes = {
  title: PropTypes.string,
  subtitle: PropTypes.string,
  projectId: PropTypes.string,
  showSearch: PropTypes.bool
};

export default Header;